body {
  background-color: #222;
}

.banner-bg {
  --s: 150px; /* control the size */
  --c1: #1d1d1d;
  --c2: #4e4f51;
  --c3: #3c3c3c;
  background:
    repeating-conic-gradient(from 30deg,#0000 0 120deg,var(--c3) 0 180deg) 
     calc(.5*var(--s)) calc(.5*var(--s)*0.577),
    repeating-conic-gradient(from 30deg,var(--c1) 0 60deg,var(--c2) 0 120deg,var(--c3) 0 180deg);
  background-size: var(--s) calc(var(--s)*0.577);
  border: 10px solid darkslategray;
}

.app-container-start {
  background-color: #5a615b;
  min-height: 30vh;
}

.text-inlay {
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background: linear-gradient(
  to bottom,
  #97aa89,
  #97aa89 50%,
  #627765 50%,
  #627765
  );
  background-size: 20% 15px;
  background-clip: text;
  display: block;
  text-shadow: 2px -2px 10px white;
}

.cantata-one-regular {
  font-family: "Cantata One", serif;
  font-weight: 400;
  font-size: 28px;
  font-style: normal;
}

.step-1 {
  font-family: 'PT Serif', serif, sans-serif;
}

.learn-more {
  font-family: 'Lora', sans-serif;
  color: #2196f3;
}

.step-2 {
  font-family:'Lucida Sans', sans-serif, Verdana;
  font-size: 14px;
}

.media-box {
  background: #464646;
  flex: 1 100%;
  position: relative;
  background: linear-gradient(to bottom, #85a19f, #9fb8cf);
  padding: 10px;
  margin-top: 20px;
  border-top: 10px solid gray;
}

.flex-mobi {
  display: flex;
  flex-direction: column;
}

.modal-style {
  display:flex;
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #3b3b3bd0;
  width: 700px;
  border: 3px solid #000000ad;
  flex-direction: column;
  padding: 20px;
  z-index: 5;
}

.modal-style-2 {
  background-color: #4d4d4deb;
}

.mytbl{
  max-height: calc(100vh - 120px);
  overflow-y: scroll;
}

.app-container {
  background-color: #5a615b;
  min-height: 100vh;
}

.card {
  background-color: whitesmoke !important;
  margin: 5px;
}

.alternate {
  background-color: #c4c4c4 !important;
  margin: 10px;
}

.last-font {
  font-family: Verdana, sans-serif;
  font-size: 13px;
  padding: 10px;
  margin-bottom: -10px;
}

.choose-plus {
  font-family:'Lucida Sans', Geneva, sans-serif;
  font-size: 13px;
  padding: 10px;
}

.title-font {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.artist-font {
  font-family: Geneva, sans-serif;
  color: #242a61;
  font-weight: 550;
}

.song-tile {
  padding: 5px;
  flex: 0 100%;
}

.song-tile-font {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.song-tile-artist {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.book-tile {
  padding: 5px;
  flex: 0 100%;
}

.book-tile-font {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.book-tile-artist {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.audio-file {
  border-bottom: 3px solid #183492;
}

.menu-book {
  border-bottom: 3px solid #183492;
}

@media screen and (max-width: 980px) {
}

@media screen and (max-width: 768px) {
  .flex-mobi {
    flex-direction: column;
  }
  .modal-style {
    width: 600px;
  }
  .media-box {
    margin-top: 0px;
    border-top: 0px;
  }
}

@media screen and (max-width: 575px) {
  .modal-style {
    width: 300px;
  }
  .song-tile-font {
    font-family: monospace, sans-serif, Verdana;
  }
  .song-tile-artist {
    font-family: monospace, sans-serif, Verdana;
  }
  .book-tile-font {
    font-family: monospace, sans-serif, Verdana;
  }
  .book-tile-artist {
    font-family: monospace, sans-serif, Verdana;
  }
}